import React from "react";

import { ReactComponent as PasinoChristmasTree } from "assets/logos/christmas/christmasTree.svg";
import { ReactComponent as PasinoChristmasBranch } from "assets/logos/christmas/christmasBranch.svg";

import css from "./styles.module.scss";

export const Christmas = () => {
  return (
    <div className={css.root}>
      <PasinoChristmasTree className={css.tree} />
      <PasinoChristmasBranch className={css.branch} />
    </div>
  );
};
